import React from 'react'
import './about.css'
import ME from '../../assets/meAbout.png'
import {FaAward} from 'react-icons/fa'
import {BiLibrary} from 'react-icons/bi'

const About = () => {
  return (
    <section id='about'>
      <h5>Kenali</h5>
      <h2>Tentang Saya</h2>

      <div className="container aboutContainer">
        <div className="aboutMe">
          <div className="aboutMeImage">
            <img src={ME} alt="ME" />
          </div>
        </div>
        <div className="aboutContent">
          <div className="aboutCards">
            <article className='aboutCard'>
              <FaAward className='aboutIcon' />
              <h5>Pengalaman</h5>
              <small>+2 Tahun Ngoding</small>
            </article>
            <article className='aboutCard'>
              <BiLibrary className='aboutIcon' />
              <h5>Projek</h5>
              <small>+4 Projek Dikerjakan</small>
            </article>
            
          </div>
          <p>Saya adalah mahasiswa dari Universitas Negeri Jakarta yang beralamat di Rawamangun, Jakarta Timur. Mengambil jurusan Ilmu Komputer awalnya karena ingin sesuatu yang berbeda dalam mempelajari matematika. Semakin hari semakin menikmati akan lingkungan pembelajaran ini karena berkaitan dengan era 4.0. Berharap ilmu yang didapat dapat berguna dan terimplementasi dengan sebagaimana mestinya.</p>
          <a href="https://api.whatsapp.com/send?phone=6285883534167" className='btn btn-primary'>Sapa</a>
        </div>
      </div>
    </section>
  )
}

export default About