import React from 'react'
import './contact.css'
import {MdOutlineEmail} from 'react-icons/md'
import {AiOutlineWhatsApp} from 'react-icons/ai'
import { useRef } from 'react'
import emailjs from 'emailjs-com'

const Contact = () => {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_vhm1mfp', 'template_9q8tlpe', form.current, 'koBInXp7qFyYhG3Eq')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
    e.target.reset()
  };

  return (
    <section id='contact'>
      <h5>Ayo Saling Terhubung</h5>
      <h2>Hubungi Saya</h2>

      <div className="container contactContainer">
        <div className="contactOptions">
          <article className="contactOption">
            <MdOutlineEmail className='contactOptionIcon' />
            <h4>Email</h4>
            <h5 className='text-light'>moh.falendwandamen_1313618027@mhs.unj.ac.id</h5>
            <div className='contactOptionButton'>
              <a href="mailto:moh.falendwandamen_1313618027@mhs.unj.ac.id" target='_blank'>Kirim pesan</a>
            </div>
          </article>
          <article className="contactOption">
            <AiOutlineWhatsApp className='contactOptionIcon' />
            <h4>Whatsapp</h4>
            <h5 className='text-light'>+6285883534167</h5>
            <div className='contactOptionButton'>
              <a href="https://api.whatsapp.com/send?phone=6285883534167" target='_blank'>Kirim pesan</a>
            </div>
          </article>
        </div>
        <form ref={form} onSubmit={sendEmail}>
          <input type="text" name="name" placeholder='Nama lengkap' required />
          <input type="email" name="email" placeholder='Email' required />
          <textarea name="message" id="" cols="30" rows="10" placeholder='Tulis pesan' required></textarea>
          <button type="submit" className='btn btn-primary'>Kirim Pesan</button>
        </form>
      </div>
    </section>
  )
}

export default Contact