import React from 'react'
import './header.css'
import CTA from './CTA'
import ME from '../../assets/me.png'
import HeaderSocials from './HeaderSocials'

const Header = () => {
  return (
    <header>
      <div className="container headerContainer">
        <h5>Halo, saya</h5>
        <h1>Moh Falend Wandamen</h1>
        <h5 className="text-light">Web Developer</h5>
        <CTA />
        <HeaderSocials />
        <div className="me">
          <img src={ME} alt="saya" />
        </div>
        <a href="#contact" className='scrollDown'>Geser ke Bawah</a>
      </div>
    </header>
  )
}

export default Header